import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { Global } from './global.service';
import 'rxjs/add/operator/map';
// import { UploadService } from './upload.service';
// import Swal from 'sweetalert2';


@Injectable({
  providedIn: 'root'
})
export class ApiService {
  public url: String;

  constructor(private http: HttpClient) {
    this.url = Global.url;
  }

 //Canales TV y Emisoras
  getIdiomas(): Observable<any> {

    let headers = new HttpHeaders().set("Content-Type", "application/json");

    return this.http.get(this.url + "idiomas", { headers: headers }).map(
      (resp: any) => resp.results
    );
  }  
  getEmisoras(id): Observable<any> {   
    let headers = new HttpHeaders().set("Content-Type", "application/json");
    return this.http.get(this.url + "emisoras/"+id, { headers: headers }).map(
      (resp: any) => resp.results
    );
  }
  getTv(id): Observable<any> {
    
    let headers = new HttpHeaders().set("Content-Type", "application/json");
    return this.http.get(this.url + "tv/"+id, { headers: headers }).map(
      (resp: any) => resp.results
    );
  }
  getContenido(id):Observable<any>{

    let headers = new HttpHeaders().set("Content-Type", "application/json");
    return this.http.get(this.url + "multimedia/"+id, { headers: headers });   
  

  }
  //publicidad
  getBanners(): Observable<any> {
    let headers = new HttpHeaders().set("Content-Type", "application/json");
    return this.http.get(this.url + "banners", { headers: headers }).map(
      (resp: any) => resp.results
    );
  }
  getVideoPublicidad(): Observable<any> {
    let headers = new HttpHeaders().set("Content-Type", "application/json");
    return this.http.get(this.url + "videosp", { headers: headers }).map(
      (resp: any) => resp.results
    );
  }

  //tienda
  getProductos(id): Observable<any> {

    let headers = new HttpHeaders().set("Content-Type", "application/json");

    return this.http.get(this.url + "productos/"+id, { headers: headers }).map(
      (resp: any) => resp.results
    );
  }
  //videos a la carta 
  getVideos(id): Observable<any> {

    let headers = new HttpHeaders().set("Content-Type", "application/json");

    return this.http.get(this.url + "video/"+id, { headers: headers }).map(
      (resp: any) => resp.results
    );
  } 
  getCategorias(): Observable<any> {

    let headers = new HttpHeaders().set("Content-Type", "application/json");

    return this.http.get(this.url + "categorias", { headers: headers }).map(
      (resp: any) => resp.results
    );
  }
  getPCategorias(): Observable<any> {

    let headers = new HttpHeaders().set("Content-Type", "application/json");

    return this.http.get(this.url + "pcategorias", { headers: headers }).map(
      (resp: any) => resp.results
    );
  }
  crearOrdenes(data):Observable<any>{

    let params = JSON.stringify(data);

    let headers = new HttpHeaders().set("Content-Type", "application/json");

    return this.http.post(this.url+"ordenes",params,{headers:headers});
  }
  getOrdenes(id): Observable<any> {

    let headers = new HttpHeaders().set("Content-Type", "application/json");

    return this.http.get(this.url + "ordenes/"+id, { headers: headers }).map(
      (resp: any) => resp.results
    );
  }
  getProductosOrdenes(id): Observable<any> {

    let headers = new HttpHeaders().set("Content-Type", "application/json");

    return this.http.get(this.url + "compras/"+id, { headers: headers }).map(
      (resp: any) => resp.results
    );
  }
  getAlquileres(id,categoria): Observable<any> {

    console.log(id+'-'+categoria);

    let headers = new HttpHeaders().set("Content-Type", "application/json");

    return this.http.get(this.url + "alquileres/"+id+"/"+categoria, { headers: headers }).map(
      (resp: any) => resp.results
    );
  }
  getProducto(id): Observable<any> {

    let headers = new HttpHeaders().set("Content-Type", "application/json");

    return this.http.get(this.url + "cartavideo/"+id, { headers: headers }).map(
      (resp: any) => resp.results
    );
  }
  
  




}
