import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApiService } from 'src/app/services/api.service';

@Component({
  selector: 'app-loadmovie',
  templateUrl: './loadmovie.component.html',
  styleUrls: ['./loadmovie.component.scss'],
  providers:[ApiService]
})
export class LoadmovieComponent implements OnInit {
  snapshotParam = "initial value";
  subscribedParam = "initial value";
  item:any=[];
  url_video="";

  constructor(private route:ActivatedRoute,private api:ApiService) { }

  ngOnInit() {
    this.snapshotParam = this.route.snapshot.paramMap.get("source");
    // Subscribed
    this.route.paramMap.subscribe(params => {
    this.subscribedParam = params.get("source");
    });
    this.getProducto(this.subscribedParam);

  }

  getProducto(id) {
    this.api.getProducto(id).subscribe((resp:any)=>{
     this.item=resp[0]; 
      
     this.url_video = `https://player.vimeo.com/video/${this.item.url}?autoplay=1&title=0&byline=0&portrait=0`;

     console.log(this.item);
     
    });
  }



}
