import { Component, OnInit } from '@angular/core';
import { VgAPI, VgPlayer, VgEvents, VgFullscreenAPI } from 'videogular2/compiled/core';
import { ActivatedRoute } from '@angular/router';
import { ApiService } from 'src/app/services/api.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-player',
  templateUrl: './player.component.html',
  styleUrls: ['./player.component.scss'],
  providers:[ApiService]
})
export class PlayerComponent implements OnInit {
src="http://familymedia.orione.cf/api/img/videos/12345.mp4";
api:VgAPI;
loading:boolean=true;
video:boolean=false;
audio:boolean=false;
fulls:boolean=true;
active:boolean=false;
ads:boolean=true;
snapshotParam = "initial value";
subscribedParam = "initial value";
source={
  url:"",
  imagen:""
}
  stado: boolean;


  constructor(private fsApi: VgFullscreenAPI,private route:ActivatedRoute,private contenido:ApiService) {

   }

  ngOnInit() {
    
    this.snapshotParam = this.route.snapshot.paramMap.get("source");
    // Subscribed
    this.route.paramMap.subscribe(params => {
      this.subscribedParam = params.get("source");
    });
    

  }

  getSource(id){
    this.contenido.getContenido(id).subscribe((resp:any)=>{
      console.log(resp);
     if(resp.ok==true){
      let tipo= resp.results[0].tipo;
      this.source= {
        url:resp.results[0].url,
        imagen:resp.results[0].imagen
      }     

      if (tipo==1) {
        this.setAudio();
      }else if(tipo==2) {
        this.setVideo();
      }  
     }

    });

  }

  onPlayerReady(player: VgAPI) {

    this.api = player;
    this.api.getDefaultMedia().subscriptions.loadStart.subscribe((a:any)=>{
      this.playAudio();
    });

    let subscriptions= this.api.getDefaultMedia();

    // this.api.getDefaultMedia().subscriptions.play.subscribe( a=>{
    //   console.log("Esta PLay");
    //   this.active=true;
    // }); 


    // subscriptions.subscriptions.pause.subscribe(a=>{
    //   this.active=false;
    // });
             
    subscriptions.subscriptions.emptied.subscribe(a=>{
      console.log("Esta vacio")
    });

    subscriptions.subscriptions.ended.subscribe(a=>{
      this.ads=false;
      this.getSource(this.subscribedParam);
    });
    

    this.api.getDefaultMedia().subscriptions.loadedMetadata.subscribe((a:any)=>{    
      this.playAudio();
    },error=>{
      console.log(error);
    }); 
    
    
    this.api.fsAPI.onChangeFullscreen.subscribe((a:any)=>{
      if(this.api.fsAPI.isFullscreen){
        this.fulls=false;
      }else{
        this.fulls=true;
      }


    });


  }
  playAudio() {        
    this.api.play();
  }
  errorAudio(event){
    Swal.fire('Radio Error Prueba otra emisora');    
  }
  stopAudio(){
    this.api.pause();  
    this.stado=false;
  }
  setAudio(){
    this.loading=false;
    this.audio=true;
  }
  setVideo(){
    this.loading=false;
    this.video=true;
  }

}
