import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { StartComponent } from './secciones/start/start/start.component';
import { LoginComponent } from './secciones/start/login/login.component';
import { RegisterComponent } from './secciones/start/register/register.component';
import { DashboardComponent } from './secciones/dashboard/pages/dash/dashboard.component';
import { HomeComponent } from './secciones/dashboard/pages/home/home.component';
import { TvComponent } from './secciones/dashboard/pages/tv/tv.component';
import { RadioComponent } from './secciones/dashboard/pages/radio/radio.component';
import { AccountComponent } from './secciones/dashboard/pages/account/account.component';
import { MiscomprasComponent } from './secciones/dashboard/pages/miscompras/miscompras.component';
import { PlayerComponent } from './secciones/dashboard/pages/player/player.component';
import { AdsComponent } from './secciones/ads/ads.component';
import { StoreComponent } from './secciones/dashboard/pages/store/store.component';
import { ReviewComponent } from './secciones/dashboard/pages/review/review.component';
import { CarritoComponent } from './secciones/dashboard/pages/carrito/carrito.component';
import { LoadmovieComponent } from './secciones/dashboard/pages/loadmovie/loadmovie.component';



const routes: Routes = [
  {path:'',component:StartComponent},
  {path:'login',component:LoginComponent},
  {path:'register',component:RegisterComponent},
  {path:'principal',component:DashboardComponent,children:[
    {path:'',component:HomeComponent},
    {path:'home',component:HomeComponent},
    {path:'tv',component:TvComponent},
    {path:'radio',component:RadioComponent},
    {path:'account',component:AccountComponent},
    {path:'compras',component:MiscomprasComponent},
    {path:'store',component:StoreComponent},
    {path:'review/:source',component:ReviewComponent},
    {path:'carro',component:CarritoComponent},
    {path:'load/:source',component:LoadmovieComponent},
    {path:'player/:source',pathMatch:'full',component:PlayerComponent}
  ]},
  {path:'ads/:tipo',component:AdsComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
