import { Component, OnInit, ViewChild } from '@angular/core';
import { ApiService } from 'src/app/services/api.service';
import { LocalStorageService } from 'src/app/services/local-storage.service';
import { ModalDirective } from 'angular-bootstrap-md';

@Component({
  selector: 'app-miscompras',
  templateUrl: './miscompras.component.html',
  styleUrls: ['./miscompras.component.scss'],
  providers:[ApiService,LocalStorageService]
})
export class MiscomprasComponent implements OnInit {
  @ViewChild('basicModal',{static: false}) basicModal: ModalDirective;
  usuario:any=[];
  ordenes:any=[];
  productos:any=[];

  constructor(private api:ApiService,private local:LocalStorageService) {
    this.getUser();
   }

  ngOnInit() {
  }

  getOrdenes(){

    this.api.getOrdenes(this.usuario.email).subscribe((resp:any)=>{

      this.ordenes=resp;

    });


  }

  getUser(){
    this.local.cargarStorage('usuario').then((data:any)=>{
      this.usuario=data;  
      this.getOrdenes();
    });
  }

  vermas(id){

    this.api.getProductosOrdenes(id).subscribe((resp:any)=>{

      this.productos=resp;

      this.basicModal.show();
      console.log(this.productos);
    });

  }



}
