import { Component, OnInit, Input } from '@angular/core';
import { ApiService } from 'src/app/services/api.service';
import { LocalStorageService } from 'src/app/services/local-storage.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-alquileres',
  templateUrl: './alquileres.component.html',
  styleUrls: ['./alquileres.component.scss'],
  providers:[ApiService,LocalStorageService,]
})
export class AlquileresComponent implements OnInit {
  @Input() categoria = {
    id: "",
    categoria: ""
  }; 
 
  usuario:any=[];
  items:any=[];
  url:any="";
  constructor(private api:ApiService,private local:LocalStorageService,private router:Router) {
    this.getUser();
   }

  ngOnInit() {   

  }

  getUser(){
    this.local.cargarStorage('usuario').then((data:any)=>{
      this.usuario=data;   
      this.Alquileres(this.usuario.id,this.categoria.id);
    });
  }
  Alquileres(id,cat){ 
    this.api.getAlquileres(id,cat).subscribe((resp:any)=>{
      this.items=resp;   
    });
    
  }
  openload(id){
    this.router.navigate(['/principal/load',id]);
  }


}
