import { Component, OnInit } from '@angular/core';
import { StoreService } from 'src/app/services/store.service';
import { ApiService } from 'src/app/services/api.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-review',
  templateUrl: './review.component.html',
  styleUrls: ['./review.component.scss'],
  providers: [StoreService, ApiService]
})
export class ReviewComponent implements OnInit {
  snapshotParam = "initial value";
  subscribedParam = "initial value";
  item:any=[];

  constructor(private store: StoreService, private api: ApiService,private route:ActivatedRoute) { }
  ngOnInit() {
    this.snapshotParam = this.route.snapshot.paramMap.get("source");
    // Subscribed
    this.route.paramMap.subscribe(params => {
    this.subscribedParam = params.get("source");
    });
    this.getProducto(this.subscribedParam);

  }

  getProducto(id) {
    this.api.getProducto(id).subscribe((resp:any)=>{
     this.item=resp[0];     
    });
  }


  agregar(id) {
    this.store.addproducto(id).subscribe(resp => {
    }, error => {
      console.log(error);
    });
  }

}
