import { Injectable } from '@angular/core';
import {Platform} from '@angular/cdk/platform';
import {HttpClient,HttpHeaders,HttpBackend } from '@angular/common/http';
import {Observable}from 'rxjs/Observable';
import {Global} from './global.service';
import { Storage } from '@ionic/storage';
import 'rxjs/add/operator/map';

@Injectable({
  providedIn: 'root'
})
export class LoginService {
  public url:String;
  private http:HttpClient;
  


   constructor(handler: HttpBackend,public platform:Platform,private storage: Storage) {
    this.url= Global.url;
    this.http= new HttpClient(handler);
   }
   login(data):Observable<any>{
    
    let params = JSON.stringify(data);
    let headers = new HttpHeaders().set("Content-Type","application/json");
    
    return this.http.post(this.url+'login',params,{headers:headers}).map((resp:any)=>{

    console.log(resp);

      if(this.platform.isBrowser){
        localStorage.setItem('token',resp.token);
        localStorage.setItem('usuario',JSON.stringify({
          name:resp.name,
          surname:resp.surname,
          email:resp.email,
          telefono:resp.telefono,
          id:resp.id
        }));        
        return true;
        
      }else{
          this.storage.set('token',resp.token);
          this.storage.set('usuario',JSON.stringify({
            name:resp.name,
            surname:resp.surname,
            email:resp.email,
            telefono:resp.telefono,
            id:resp.id
          }));       
      
        return true;     
      }
    
    }); 
   }
   register(data):Observable<any>{

    let params= JSON.stringify(data);
    let headers = new HttpHeaders().set("Content-Type","application/json");

    return this.http.post(this.url+'register',params,{headers:headers}).map((resp:any)=>resp);
   }
   checkEmail(email:String){

    let headers = new HttpHeaders().set("Content-Type","application/json");

    return this.http.get(this.url+'check/'+email,{headers:headers});

   }
 
}
