import { Component, OnInit } from '@angular/core';
import { LocalStorageService } from 'src/app/services/local-storage.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-account',
  templateUrl: './account.component.html',
  styleUrls: ['./account.component.scss'],
  providers:[LocalStorageService]
})
export class AccountComponent implements OnInit {
  usuario:any=[];
  profile:FormGroup;

  constructor(private local:LocalStorageService) {
    this.getUsuario();
    
    this.profile= new FormGroup({
      name: new FormControl(this.usuario.name,Validators.required),
      surname: new FormControl(this.usuario.surname,Validators.required),
      email:new FormControl(this.usuario.email,[Validators.required,Validators.email]),
      fecha: new FormControl(null,Validators.required),
      telefono: new FormControl(this.usuario.telefono,Validators.required),
      direccion:new FormControl(null,Validators.required),
      direccion2:new FormControl(null),
      pais:new FormControl(null),
      ciudad:new FormControl(null),
      zip:new FormControl(null),
      telefono2:new FormControl(null)
    });

    

   }

  ngOnInit() {
  }
  getUsuario(){

    this.local.cargarStorage('usuario').then((data:any[])=>{
      this.usuario=data;     
    }).catch(err=>{
      throw err;
      
    });  
 
   }

   actualizar(){
  
   }



}
