import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { Global } from './global.service';
import Swal from 'sweetalert2';
import 'rxjs/add/operator/map';
import { LocalStorageService } from './local-storage.service';




@Injectable({
  providedIn: 'root'
})
export class StoreService {
  productos: any = [];
  url: string;

  constructor(private http: HttpClient, private local: LocalStorageService) {
    this.url = Global.url;
  }  
  addproducto(id): Observable<any> {

    this.local.cargarStorage('carrito').then((data: any) => {
      this.productos = data;
    }).catch(error => {
      console.log(error);
    });

    let existe = this.productos.findIndex(e => e.id == id);
  

    if (existe === -1) {
      let headers = new HttpHeaders().set("Content-Type", "application/json");

      return this.http.get(this.url + "cartavideo/" + id, { headers: headers }).map((resp: any) => {       
        this.productos.push(resp.results[0]);
        this.local.SaveStorage('carrito', this.productos);
        Swal.fire({
          position: 'top-end',
          type: 'success',
          title: 'Producto agregado correctamente',
          showConfirmButton: false,
          timer: 1500
        });
      });
    } else {
      Swal.fire({
        position: 'top-end',
        type: 'info',
        title: 'Este Producto ya esta en el carrito de compras',
        showConfirmButton: false,
        timer: 1500
      });
      return new Observable(observer => {
        observer.next(true);
      });
    }
  }




}
