import { Component, OnInit,OnDestroy , HostBinding, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { StoreService } from 'src/app/services/store.service';
import { Subscription } from 'rxjs';
import { LocalStorageService } from 'src/app/services/local-storage.service';




@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss'],
  providers:[StoreService,LocalStorageService]
})
export class DashboardComponent implements OnInit {
  public contador = 0;
  public subscription: Subscription;
  productos:any=[];
  logo="logo.png";

  flag:boolean=true;


  constructor(private router:Router,private store:StoreService,private local:LocalStorageService) {
    // this.router.navigate(['/principal/home']);
    this.local.cargarStorage('carrito').then((data:any)=>{
      this.productos=data;
      this.contador=this.productos.length;
        });
   }

  ngOnInit() {
   
  }










}
