import { Component, OnInit, ViewChild } from '@angular/core';
import { LocalStorageService } from 'src/app/services/local-storage.service';
import { HttpClient } from '@angular/common/http';
import { ApiService } from 'src/app/services/api.service';
import { ModalDirective } from 'angular-bootstrap-md';
import { Router } from '@angular/router';

@Component({
  selector: 'app-carrito',
  templateUrl: './carrito.component.html',
  styleUrls: ['./carrito.component.scss'],
  providers:[LocalStorageService,ApiService]
})
export class CarritoComponent implements OnInit {
  productos:any=[];
  total1:number=0;
  total:number=0;
  productos_detalle:any=[];
  amount:number;
  public payuform: any = {};
  disablePaymentButton: boolean = true;
  data:any=[];
  cantidad:number=0;
  precio:number=0;
  usuario:any=[];
  datos_compra:any=[];
  compra_preparada:boolean=false;
   paymentPayload = {
    merchantId:"",
    accountId:"",
    referenceCode:"",
    signature:"",
    responseUrl:"",
    confirmationUrl:"",
    description:"",
    tax:0,
    taxReturnBase:0,
    currency:"",
    test:1,
    amount: "", 
    buyerEmail: "",     
  }
  @ViewChild('basicmodal',{static: false}) basicmodal: ModalDirective;

  constructor(private local:LocalStorageService,private http:HttpClient,private api:ApiService,private router:Router) {
    this.getCarrito();
    this.getUser();
   }

  ngOnInit() {
  }

  getCarrito(){
    this.local.cargarStorage('carrito').then((data:any)=>{
      this.productos=data;
    });
  }

  getUser(){
    this.local.cargarStorage('usuario').then((data:any)=>{

      this.usuario=data;   

    });
  }

  cambiar(event,id,precio,name){  
    
    let data = {
      id:id,
      precio:precio,
      name:name,
      cantidad:event.target.value
    }  
    
 
    
    let existe = this.productos_detalle.findIndex(e => e.id == data.id);   

    if (existe != -1) {
      this.productos_detalle[existe] = {       
        id: data.id,        
        precio: data.precio,  
        name:data.name,     
        cantidad: data.cantidad,
        subtotal: (data.cantidad) * data.precio
      };
    } else {
      this.productos_detalle.push({       
        id: data.id,      
        precio: data.precio,  
        name:data.name,    
        cantidad: data.cantidad,             
        subtotal: (data.cantidad) * data.precio
      });
    }
    
    this.total1=0;
    this.productos_detalle.forEach(a => {
    this.total1 += a.subtotal     
    });
    this.total = this.total1;    
 
  }
  eliminarproducto(id) {    
  
    let eliminar = this.productos_detalle.find(e => e.id == id);     
    if(!eliminar){
      this.cantidad=0;
      this.precio=0;
    }else{
      this.cantidad = eliminar.cantidad; 
      this.precio= eliminar.precio; 
    }

    this.total1 = this.precio * this.cantidad;
    let nuevo = this.productos_detalle.filter(
      productos_detalle => productos_detalle.id !== id
    );
    this.productos_detalle = nuevo;
    this.total1=0;
    this.productos_detalle.forEach(a => {
      this.total1 += a.subtotal     
    });
    this.total = this.total1;   
    this.local.eliminarStorage('carrito');

    let newStorage= this.productos.filter(
      productos =>productos.id !==id
    );
    this.productos= newStorage;   
    this.local.eliminarStorage('carrito');
    this.local.SaveStorage('carrito',this.productos); 
  }

  confirmPayment() {

    this.basicmodal.show();
    

    this.datos_compra={

      productos:this.productos_detalle,
      email_usuario:this.usuario.email,
      amount:this.total
    }  

    this.api.crearOrdenes(this.datos_compra).subscribe((resp:any)=>{

      if(resp.ok==true){

        this.basicmodal.hide();

        this.compra_preparada= true;

        this.paymentPayload = {
          merchantId:resp.merchantId,//0
          accountId:resp.accountId,//0
          referenceCode:resp.referenceCode,//0
          signature:resp.signature,//0
          responseUrl:resp.responseUrl,
          confirmationUrl:resp.confirmationUrl,  
          description:resp.description,//1
          tax:0,//2
          taxReturnBase:0,//2
          currency:resp.currency,//0
          test:1,
          amount: resp.amount,//1   
          buyerEmail: this.usuario.email//0        
        }
        console.log(this.paymentPayload);
     

      }

    });

    

   
   
  }

  direccionar(){

    this.local.eliminarStorage('carrito');    
    this.router.navigate(['/principal/home']);
  }



}
