import { Component, OnInit } from '@angular/core';
import { LoginService } from 'src/app/services/login.service';
import { Router } from '@angular/router';
import { LocalStorageService } from 'src/app/services/local-storage.service';
import { FormGroup, FormControl, Validators, AbstractControl } from '@angular/forms';
import Swal from 'sweetalert2';
import { Observable } from 'rxjs';




@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  providers:[LoginService]
})
export class LoginComponent implements OnInit {
  usuario:any=[];
  login:FormGroup;
  emailcheck:FormGroup;
  logo="logo.png";

  constructor(private loginservice:LoginService,public router:Router) { 

    this.login= new FormGroup({
      email: new FormControl(null,[Validators.email,Validators.required],this.Existeusuario.bind(this)),
      password: new FormControl(null,[Validators.required,Validators.minLength(8)])
    });
  }

  ngOnInit() {
  }

  Existeusuario(control:AbstractControl){

      return this.loginservice.checkEmail(control.value).map((resp:any)=>{
        return resp.email ? null : { emailTaken: true };
      })
      
  }

  Login(){  
    this.loginservice.login(this.login.value).subscribe(resp=>{
      this.usuario=resp;    
      // this.storage.SaveStorage(this.usuario);
      this.router.navigate(['/principal/home']);
    },error=>{
      Swal.fire({
        type: 'error',
        title: 'Oops...',
        text: 'Usuario ó Contraseña Incorrecta!'
      })

    })

  }



}
