import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { StoreService } from 'src/app/services/store.service';
import { ApiService } from 'src/app/services/api.service';
import { ModalDirective } from 'angular-bootstrap-md';
import { Router } from '@angular/router';



@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.scss'],
  providers: [StoreService, ApiService]
})
export class ProductsComponent implements OnInit {
  @ViewChild('basicModal',{static:false}) basicModal: ModalDirective;
  @Input() categoria = {
    id: "",
    categoria: ""
  };
  items:any = [];
  item: any = [];


  constructor(private store: StoreService,private api: ApiService,private router:Router) {
   
  }

  ngOnInit() {  
    this.GetProductos(this.categoria.id);   
    
  }

  OpenModal(id) {
    this.router.navigate(['/principal/review',id]);
  }

  GetProductos(id) {    
    this.api.getVideos(id).subscribe(resp => {
      this.items = resp; 
      
    })

  }

  agregar(id) {
    this.store.addproducto(id).subscribe(resp => {
    }, error => {
      console.log(error);
    });
  }




}
