import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { LoginService } from 'src/app/services/login.service';
import Swal from 'sweetalert2';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss'],
  providers:[LoginService]
})
export class RegisterComponent implements OnInit {
  logo="logo.png";
  register:FormGroup;
  constructor(private login:LoginService,public router:Router) {
    this.register= new FormGroup({
      name: new FormControl(null,Validators.required),
      surname: new FormControl(null,Validators.required),
      email:new FormControl(null,[Validators.required,Validators.email]),
      password: new FormControl(null,[Validators.required,Validators.minLength(7)]),
      telefono: new FormControl(null,[Validators.required,Validators.minLength(5)])
    });
   }

  ngOnInit() {
  }

  registrarse(){

    this.login.register(this.register.value).subscribe((resp:any)=>{

          if(resp.ok==true){
          Swal.fire({
            type: 'success',
            title: 'Usuario Creado ',
            text: 'Has creado el usuario correctamente empieza a utilizar nuestros servicios !!'
          })
          this.router.navigate(['/']);
        }
    },error=>{  
      let err = error.error.error;

      if(err=="ER_DUP_ENTRY"){
        Swal.fire({
          type: 'info',
          title: 'Correo Existente !!',
          text: 'El correo ingresado ya existe, intenta otro nuevo'
        });
      }else{
        Swal.fire({
          type: 'error',
          title: 'Oops...',
          text: 'Error desconocido Contacta con soporte'
        });

      }    

   
    });

  }

}
